
@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');
@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
@import '_variables';
@import 'functions';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

html, #root {
  font-family: $font-raleway;
  color: color(textColor);
  font-size: 17px;
  word-spacing: 1px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

.main-container {
  margin-bottom: 6rem;
}
.container {
  padding: 1.5%;
}

@media screen and (max-width: 450px) {
  html, #root {
    font-size: 15px;
  }
  footer {
    font-size: 14px;
  }

  .container {
    padding: 0 3%;
  }
}

@media screen and (max-width: 550px) {
  .container {
    padding: 0 3%;
  }
}


body {
  height: 100vh;
  background-color: color(bgColor);
  color: color(textColor);
  transition: $transition;
}


.bgColor {
  background-color: color(bgColor);
}
.textColor {
  color: color(textColor);
}

.bgMain {
  background-color: color(main);
}

.textMain {
  color: color(main);
}

a.in-button-main {
  text-decoration: none;
  background-color: color(main);
  padding:10px 30px;
  color: color(snowColor) !important;
  font-family: $font-nunito;
  font-size: 22px;
  border-radius: 40px;
  transition: $transition;
  // box-shadow: ;
  &:hover {
    transform: scale(1.1);
    opacity: .9;
  }
}



.button {
  font-size: 17px;
  font-weight: 200;
  letter-spacing: 1px;
  padding: 13px 30px 13px 30px !important;
  outline: 0;
  border: 1px solid color(textColor);
  cursor: pointer;
  position: relative;
  background-color: rgba(0, 0, 0, 0);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  text-decoration: none;
  
}

.button:after {
  content: "";
  width: 100%;
  z-index: -1;
  position: absolute;
  height: 100%;
  top: 7px;
  left: 7px;
  transition: 0.4s;
  text-decoration: none !important;
}
.button-52:after {
  background-color: color(main);
}
.button-50:after {
  background-color: color(textColor);
  color: color(textColor);
}

.button-52 {
  color: color(snowColor);
}
.button-50 {
  color: color(bgColor);
  font-weight: 500;
}
.button:hover:after {
  top: 0px;
  left: 0px;
}

.button-52:hover {
  color: color(snowColor);
}
.button-50:hover {
  color: color(bgColor);
}



a:hover {
  text-decoration: none;
  color: unset;
}

.title-0 {
  font-size: sizes(fontSizeH0);
  font-weight: 600;
