.line-small._index_1cfqm_192 {
    margin-bottom: 0 !important;
    align-items: start;
    ._divider_ucnqf_2 {
      position: relative;
      width: 85%;

      ._notch_ucnqf_30 {
        clip-path: polygon(0 -1px,80% -1px,calc(80% - 10px) 80%,10px 80%);
        width: 70px;
      }
    }
    ._indexNumber_1cfqm_201 {
      font-size: .9rem;
      line-height: .8;
    }
}
.grid-100 {
  grid-template-columns: 100% 1fr !important;
  ._notch_ucnqf_30 {
    width: 85px;
  }
}
._index_1cfqm_192 {
  position: relative;
  display: grid;
  grid-template-columns: 90px 1fr;
  gap: 16px;
  align-items: center;
  margin-bottom: 32px;
}
._divider_ucnqf_2 {
  position: relative;
  width: 100%;
  height: 2px;
  background-color: color(main);
}


._notch_ucnqf_30 {
  background-color: color(main);
  position: absolute;
  transition-property: opacity;
  transition-duration: 600ms;
  transition-timing-function: cubic-bezier(0.4, 0.0, 0.2, 1);
  transition-delay: 1160ms;
  opacity: 1;
  clip-path: polygon(0 -1px,100% -1px,calc(100% - 10px) 100%,10px 100%);
  width: 64px;
  height: 8px;
  top: 2px;
}

._indexNumber_1cfqm_201 {
  font-size: 1.2rem;
  font-weight: 500;
  color: color(main);
  opacity: 1;
  transition-property: opacity;
  transition-timing-function: cubic-bezier(0.4, 0.0, 0.2, 1);
  transition-duration: 400ms;
  transition-delay: 1.3s;
}

