footer {
  text-align: center;
  padding: 0 3% 4rem 3%;
  u {
    text-decoration: underline;
    font-weight: 500;
  }
}


.link {
  color: color(main);
  font-size: sizes(fontSizeS);
  padding: 10px;
  font-weight: 500;
  background: linear-gradient(color(textColor) 0 0) calc(100% - var(--p,0%)) / var(--p,0%) no-repeat;
  transition: 0.3s, background-position 0s;
  border: none;
  &:hover,&:focus-visible, &:focus{ 
      --p: 100%;
      color: color(bgColor);
      outline: none;
  }
  i {
      padding-right: 10px;
  }
}

.link-full {
  --p: 100%;
  color: color(bgColor);
  outline: none;
  &:hover {
    opacity: .8;
  }
}

@media screen and (max-width: 780px) {
  .link {
      color: color(bgColor);
      font-size: sizes(fontSizeXS);
      --p: 100%;
  }
}


.not-found {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  

  h1 {
      text-transform: uppercase;
      letter-spacing: 0.2em;
      font-weight: 600;
      font-size: 6em;
      margin-bottom: 0;
      color: color(main);
  }
  p {
    padding: 0 20%;
  }
}

@media screen and (max-width: 991px) {
  .not-found {
  
    p {
      padding: 0 10%;
    }
  }

  
}

@media screen and (max-width: 500px) {
  .not-found {
  
    p {
      padding: 0 0;
    }
  }

  
}